body{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(to right, hsl(318, 20%, 51%), hsl(218, 70%, 61%));
}    

.stopwatch{
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 5px solid;
    border-radius: 45px;
    background-color: white;
    padding: 30px;
}

.display{
    font-size: 5rem;
    font-family: ui-monospace;
    font-weight: bold;
    color: hsl(0, 0%, 25%);
    text-shadow: 2px 2px 2px hsla(0, 0%, 0%, 0.75);
    margin-bottom: 25px;
}

.controls button{
    font-size: 1.5rem;
    font-weight: bold;
    padding: 10px 20px;
    margin: 5px;
    min-width: 130px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    color: white;
    transition: background-color 0.5s ease;
}
 
.start-button{
    background-color: hsl(115, 100%, 40%);
    }
.start-button:hover{
    background-color: hsl(115, 100%, 30%);
    }
.stop-button{
    background-color: hsl(10, 90%, 50%);
    }
.stop-button:hover{
    background-color: hsl(10, 90%, 40%);
    }
.reset-button{
    background-color: hsl(205, 90%, 60%);
    }
.reset-button:hover{
    background-color: hsl(205, 90%, 40%);
    }
    